.backdrop-modal-is-hidden {
  display: none;
}
.backdrop-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.2);
  opacity: 1;
}
.modal-scale {
  display: none;
}
.is-hidden-modal-coctails {
  opacity: 0;
  pointer-events: none;
}

.modal-coctail-components {
  list-style-type: none;
}
.modal-coctails {
  @include mq(mob) {
    position: absolute;
    width: 320px;
    height: 990px;
    overflow: auto;
    top: 500px;
  }
  @include mq(tab) {
    width: 730px;
    height: 598px;
  }
  position: absolute;
  transition: var(--transition);
  background-color: var(--button-color-white);
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal-img {
  @include mq(mob) {
    position: absolute;
    width: 280px;
    height: 320px;
    left: 20px;
    top: 340px;
  }
  @include mq(tab) {
    position: absolute;
    width: 288px;
    height: 320px;
    left: 0px;
    top: 0px;
  }
  border-radius: 4px 4px 4px 45px;
}
.modal-coctail-name {
  @include mq(mob) {
    width: 129px;
    height: 37px;
    margin-left: 16px;
    margin-top: 30px;
  }
  @include mq(tab) {
    margin-top: 40px;
    margin-left: 300px;
  }
  color: var(--background-dark);
}
.modal-ingredients {
  @include mq(mob) {
    position: absolute;
    width: 103px;
    height: 24px;
    left: 16px;
    top: 680px;
  }
  @include mq(tab) {
    position: absolute;
    width: 103px;
    height: 24px;
    left: 320px;
    top: 105px;
    font-size: 16px;
    line-height: 1.5;
  }
  font-family: var(--title-poppins);
  font-style: normal;
  font-weight: 600;
  color: #000917;
  text-transform: uppercase;
}
.modal-per {
  @include mq(mob) {
    position: absolute;
    width: 100px;
    height: 21px;
    left: 16px;
    top: 712px;

    font-family: var(--title-poppins);
    font-style: normal;
    font-weight: 300;
    color: var(--title-secondary-light-theme);
  }
  @include mq(tab) {
    @include fonts($fs: 14px, $fw: 400, $lh: 1.5, $ls: 0.02em);
    position: absolute;
    width: 90px;
    height: 21px;
    left: 320px;
    top: 137px;
  }
}
.modal-coctail-components {
  @include mq(mob) {
    font-family: var(--title-poppins);
    position: absolute;
    width: 215px;
    height: 130px;
    left: 16px;
    top: 741px;
  }

  @include mq(tab) {
    @include fonts($fs: 16px, $fw: 400, $lh: 1.6, $ls: 0.02em);
    position: absolute;
    width: 215px;
    height: 130px;
    left: 320px;
    top: 166px;
  }

  padding: 0;
}
.modal-Instractions {
  @include mq(mob) {
    position: absolute;
    width: 122px;
    height: 26px;
    left: 16px;
    top: 87px;
  }
  @include mq(tab) {
    position: absolute;
    width: 122px;
    height: 26px;
    left: 28px;
    top: 348px;
    font-size: 16px;
    line-height: 1.6;
  }
  font-family: var(--title-poppins);
  color: var(--background-dark);
  font-style: normal;
  font-weight: 600;
  text-transform: uppercase;
}
.modal-text {
  @include mq(mob) {
    position: absolute;
    width: 275px;
    height: 168px;
    left: 16px;
    top: 137px;
  }
  @include mq(tab) {
    position: absolute;
    width: 615px;
    height: 84px;
    left: 28px;
    top: 398px;
    font-size: 16px;
    line-height: 1.75;
  }
  font-family: var(--title-poppins);
  font-style: normal;
  font-weight: 400;
  color: #5f6775;
  margin: 0;
}

.modal-menu__close {
  position: absolute;
  right: 28px;
  top: 28px;

  margin: 0;

  border: none;
  background-color: transparent;
  color: var(--title-secondary);

  transition: fill var(--transition);

  &:hover,
  &:focus {
    fill: var(--button-text-color);
  }
}

.hidden_remove {
  display: none;
}
.modal-coctail-component {
  &:hover,
  &:focus {
    color: var(--button-text-color);
   cursor:pointer;
  }
}
